.contacts__link {
  cursor: pointer;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .contacts__link:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.contacts__link.focus-visible:focus {
  opacity: 0.8;
}

.main-footer {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #ffffff;
  background-color: #030a1a;
}

.main-footer__container {
  position: relative;
  display: flex;
  width: 1180px;
  max-width: 100%;
  padding-top: 41px;
  padding-bottom: 42px;
}
@media (max-width: 1199px) {
  .main-footer__container {
    width: 100%;
    padding-top: 41px;
    padding-bottom: 42px;
  }
}
@media (max-width: 767px) {
  .main-footer__container {
    flex-direction: column;
    align-items: start;
    padding-top: 44px;
    padding-bottom: 52px;
  }
}

.main-footer__navigation {
  margin-left: 31.4%;
}
@media (max-width: 1199px) {
  .main-footer__navigation {
    margin-left: 21.2%;
  }
}
@media (max-width: 767px) {
  .main-footer__navigation {
    margin-left: 0;
    margin-bottom: 33px;
  }
}

.main-footer__navigation-list {
  display: grid;
  grid-template-columns: repeat(5, min-content);
  gap: 40px;
  margin-top: 1px;
  margin-bottom: 0;
  padding-left: 0;
  list-style-type: none;
}
@media (max-width: 1199px) {
  .main-footer__navigation-list {
    gap: 30px;
    margin-top: 2px;
  }
}
@media (max-width: 767px) {
  .main-footer__navigation-list {
    grid-template-columns: repeat(2, min-content);
    gap: 16px 76px;
  }
}

.main-footer__item--logo-item {
  position: absolute;
  top: 52px;
  left: 10px;
}
@media (max-width: 1199px) {
  .main-footer__item--logo-item {
    top: 46px;
    left: 45px;
  }
}
@media (max-width: 767px) {
  .main-footer__item--logo-item {
    position: static;
    margin-top: 5px;
  }
}
@media (max-width: 767px) {
  .main-footer__item--4 {
    grid-row: 1;
    grid-column: 2;
  }
}
@media (max-width: 767px) {
  .main-footer__item--5 {
    grid-row: 2;
    grid-column: 2;
  }
}

.main-footer__link {
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .main-footer__link:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.main-footer__link.focus-visible:focus {
  opacity: 0.8;
}
@media (max-width: 767px) {
  .main-footer__link {
    font-size: 16px;
    line-height: 19px;
  }
}

@media (max-width: 1199px) {
  .main-footer__logo-img {
    width: 82px;
    height: 18px;
  }
}
@media (max-width: 767px) {
  .main-footer__logo-img {
    width: 68px;
    height: 15px;
  }
}

.main-footer__social {
  margin-left: auto;
}
@media (max-width: 767px) {
  .main-footer__social {
    margin-left: 0;
  }
}

.social__list {
  display: grid;
  grid-template-columns: repeat(3, auto);
  gap: 11px;
  min-height: 25px;
  margin: 0;
  padding-left: 0;
  list-style-type: none;
}

.social__link {
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .social__link:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.social__link.focus-visible:focus {
  opacity: 0.8;
}

